.productContainer {
    display: flex;
    align-items: flex-start; /* Canh trên cùng */
    gap: 20px; /* Khoảng cách giữa ảnh và thông tin */
}

.productImage img {
    max-width: 400px; /* Điều chỉnh kích thước ảnh */
    height: auto;
    border-radius: 8px;
}

.productDetails {
    flex: 1; /* Lấy hết không gian còn lại */
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.productPage {
    display: flex;
    justify-content: center;
    padding: 40px 20px;
}

.productContainer {
    display: flex;
    align-items: flex-start;
    gap: 40px;
    max-width: 1200px;
    width: 100%;
}

.productImage img {
    max-width: 400px;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.productDetails {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.productTitle {
    font-size: 28px;
    font-weight: bold;
    color: #333;
    margin-bottom: 10px;
}

.productPrice {
    font-size: 24px;
    color: #d9534f;
    font-weight: bold;
    margin-bottom: 15px;
}

.productDescription {
    font-size: 16px;
    color: #666;
    line-height: 1.6;
    margin-bottom: 20px;
}

.productActions {
    display: flex;
    gap: 15px;
}

.button {
    padding: 12px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.addToCart {
    background-color: #5bc0de;
    color: white;
}

.addToCart:hover {
    background-color: #31b0d5;
}

.buyNow {
    background-color: #d9534f;
    color: white;
}

.buyNow:hover {
    background-color: #c9302c;
}
